import React from "react";
import axios from "axios";
import { getAuthorizationHeader } from "../apis/axios";

function SearchingResult() {
  const getSearchingResult = async () => {
    try {
      const Data = await axios
        .get(
          "https://ptx.transportdata.tw/MOTC/v2/Rail/TRA/Station?$top=10&$format=JSON",
          {
            headers: getAuthorizationHeader(),
          }
        )
        .then(function (response) {
          document.querySelector("body").textContent = JSON.stringify(
            response.data
          );
        })
        .catch(function (error) {
          console.log(error);
        });
      console.log(Data.data);
    } catch (error) {
      alert("GET Error!!");
    }
  };

  return (
    <div className="get">
      <button onClick={getSearchingResult}>Get</button>
    </div>
  );
}

export default SearchingResult;
